.repertory-inventory-overview {
  width: 98%;
  height: 98%;
  margin-left: 10px;
  margin-top: 10px;
  background: white;
  padding: 0 12px;
  box-sizing: border-box;
  overflow: scroll;

  &-clear {
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #096DD9;
    line-height: 14px;
    cursor: pointer;
  }

  &-header {
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: space-between;

  }

  &-form {
    height: 100%;
    display: flex;
    align-items: center;

    &Item {
      height: 100%;
      margin-left: 10px;
      display: flex;
      align-items: center;
    }
  }

  &-body {
    display: flex;
    height: calc(100% - 50px);
    position: relative;

    &-left {
      width: 55%;
      position: relative;
    }

    &-right {
      width: 40%;

      &-price {
        position: relative;
      }

      &-weight {
        position: relative;

      }
    }


    &-title {
      font-size: 16px;
      font-weight: 600;
      color: #333333;
    }
  }

  .noting {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: absolute;
    top: 0;
    font-size: 14px;
    font-weight: 400;
    color: rgba(34, 34, 34, 0.7);
    text-align: center;

    .noting-box {
      width: 100%;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      span {
        width: 107px;
        height: 107px;

        svg {
          width: 100%;
          height: 100%;
        }
      }
    }


    .ant-btn[disabled],
    .ant-btn[disabled]:hover {
      background: white;
    }

    .ant-form-item-label>label {
      font-size: 16px;
      font-family: SourceHanSansSC-Normal, SourceHanSansSC;
      font-weight: 400;
      color: #222222;
      line-height: 14px;
    }

    .ant-form-item-label>label::after {
      content: "";
    }

    .ant-form-item {
      margin-bottom: 0;
    }
  }

  .ant-form-item-label>label {
    font-size: 16px;
    font-family: SourceHanSansSC-Normal, SourceHanSansSC;
    font-weight: 400;
    color: #222222;
    line-height: 14px;
  }

  .ant-form-item-label>label::after {
    content: "";
  }

  .ant-form-item {
    margin-bottom: 0;
  }
}
@primary-color: #1890ff;